import { Component, h, State, Prop, Element, Event, EventEmitter } from '@stencil/core';
import tailwind from '../../output.css';
@Component({
  tag: 'rf-carousel',
  shadow: true,
})
export class Carousel {
  @Element() element: HTMLElement;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }

  @Prop() images: string[] = [];
  @State() currentIndex: number = 0;

   goToPrevious = () => {
    if (this.currentIndex > 0) {
      this.currentIndex--;
    }
  };

   goToNext = () => {
    if (this.currentIndex < this.images.length - 1) {
      this.currentIndex++;
    }
  };

  @Event({ bubbles: true, composed: true }) rf_show: EventEmitter<number>;
  show(){
    this.rf_show.emit(this.roomId)
  }

  @Prop() roomId: number

  render() {
    return (
      <div class="relative w-full h-full">
        <button
          onClick={this.goToPrevious.bind(this)}
          disabled={this.currentIndex === 0}
          class={{
            'absolute z-30 left-1 top-1/2 transform -translate-y-1/2 bg-gray-100 text-black p-1 rounded-full focus:outline-none': true,
            'opacity-50 cursor-not-allowed': this.currentIndex === 0,
          }}
        >
          &#10094;
        </button>
        <img src={this.images[this.currentIndex]} class="w-full h-full block object-cover" onClick={this.show.bind(this)} />
        <button
          onClick={this.goToNext.bind(this)}
          disabled={this.currentIndex === this.images.length - 1}
          class={{
            'absolute z-30 right-1 top-1/2 transform -translate-y-1/2 bg-gray-100 text-black p-1 rounded-full focus:outline-none': true,
            'opacity-50 cursor-not-allowed': this.currentIndex === this.images.length - 1,
          }}
        >
          &#10095;
        </button>
      </div>
    );
  }
}
